@font-face {
  font-family: WorkSans;
  src: url("./fonts/WorkSans-Medium.ttf");
}
@font-face {
  font-family: dotted;
  src: url("./fonts/CONMANFAT.ttf");
}

@font-face {
  font-family: Inter;
  src: url("./fonts/Inter-Black.ttf");
}

.dotted {
  font-family: dotted;
}

h1,
h2 {
  font-family: Inter;
}

h3,
h4,
h5,
h6 {
  font-family: WorkSans;
}

.btn {
  margin-right: 24px;
  padding: 8px 16px;
  border-radius: 4px;
  font-size: 18px;
  line-height: 25px;
  text-decoration: none;
  font-weight: 500;
  outline: none;
  border: none;
  transition: 0.3s;
  font-family: WorkSans;
  background-color: #cecece;
  color: #363636;
  transition: 1s;
  box-shadow: transparent;
}

.btn.green {
  background-color: #89f6aa;
  color: #12622a;
  border: none;
}

.btn.green:hover {
  background-color: #12622a;
  color: #89f6aa;
}

.btn.transparent:hover {
  background: rgb(218, 218, 218);
}

.btn.purple:hover {
  background-color: #635a92;
  color: #ffffff;
  border: none;
}

.btn.blue {
  background-color: #bce4ff;
  color: #063dd6;
  border: none;
}
.btn.blue:hover{
  background-color: #063dd6 ;
  color: #bce4ff
}

.btn.purple {
  background-color: #635a92;
  color: #f3e1ff;
  border: none;
}
.btn.purple:hover{
  background-color: #f3e1ff ;
  color: #635a92
}

.btn.gray {
  background-color: #dbdbdb;
  color: #2e2e2e;
  border: none;
}
.btn.gray:hover{
  background-color: #2e2e2e ;
  color: #dbdbdb
}

.btn.red {
  background-color: #fa9292;
  color: #721919;
  border: none;
}
.btn.red:hover{
  background-color: #721919 ;
  color: #fa9292
}

.btn.tomato {
  background-color: #ff934d;
  color: #7a2100;
  border: none;
}
.btn.tomato:hover{
  background-color: #7a2100 ;
  color: #ff934d
}

.inpGroup {
  font-family: WorkSans;
}

.floating-card {
  text-decoration: none;
  color: rgb(67, 67, 75);
  transition: 0.4s;
  margin: 10px;

}

.floating-card:hover {
  transform: rotate3d(0.1, 0.1, 0.005, -8.5deg) scale(1.03);
  border-color: rgb(42, 73, 250);
  color: rgb(67, 67, 75);
}
